.loginScreen {
  position: relative;
  height: 100%;
  background: url("https://i0.wp.com/www.nationalturk.com/en/wp-content/uploads/2022/05/netflix-codes.jpg?w=1194&ssl=1")
    center no-repeat;
  background-size: cover;
}

.loginScreen_logo {
  position: fixed;
  left: 0;
  width: 150px;
  object-fit: contain;
  padding-left: 20px;
}
.loginScreen_button {
  position: fixed;
  right: 20px;
  top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: red;
  font-weight: 600;
  border: none;
  cursor: pointer;
}
.loginScreen_gradient {
  width: 100%;
  z-index: 1;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0, 8) 0,
    rgba(0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
  );
}
.loginScreen_body {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  color: white;
  padding: 20px;
  margin-left: auto;
  margin-right: auto;
  top: 30%;
  text-align: center;
}
.loginScreen_body > h1 {
  font-size: 3.5rem;
  margin-bottom: 20px;
}
.loginScreen_body > h2 {
  font-size: 2rem;
  font-weight: 400;
  margin-bottom: 30px;
}
.loginScreen_body > h3 {
  font-size: 1.3rem;
  font-weight: 400;
}

.loginScreen_input > form > input {
  padding: 10px;
  outline-width: 0;
  height: 30px;
  width: 30%;
  border: none;
  max-width: 600px;
}

.loginScreen_getStarted {
  padding: 17px 20px;
  color: #fff;
  background-color: red;
  border: none;
  font-weight: 600;
  cursor: pointer;
}

.loginScreen_input{
    margin: 20px;
}
